*, :after, :before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
  margin: 0;
  background-color: #111317;
  color: #f1f1f1;
}

.container {
  max-width: 600px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 1rem auto;
}
.row {
  margin: 0.5rem 0;
  position: relative;
}
.logo {
  font-size: 1.25rem;
  line-height: 1.5rem;
  text-align: center;
  display: block;
  padding: 1rem;
}
.delete-button:before {
  content: "+";
  display: block;
  transform: rotate(45deg);
}

input,
button {
  font-family: sans-serif;
  font-size: 16px;
  line-height: 22px;
  border: 1px solid #aaa;
  color: #f1f1f1;
  background-color: transparent;
  transition: .3s all;
}
input[type="text"] {
  width: 100%;
  display: block;
  padding: 0.5rem 1rem;
}

::placeholder {
  color: #aaa;
}

.add-button,
.delete-button {
  font-size: 1.25rem;
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 100%;
  border: 0;
}
.add-button {
  font-size: 1rem;
}
.delete-button {
  color: red;
}